.div-center {
    display: table;
    margin: 0 auto;
}

.div-center img {
    width: 10rem;
}

.buttons-group {
    display: flex;
}

.buttons-group button:first-child {
    margin-right: 8px;
}

a, div, img, p, button {
    -moz-user-select: none;
     -ms-user-select: none;
         user-select: none;
    -webkit-user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;
}
@font-face {
    font-family: Russo; /* Гарнитура шрифта */
    src: url(/Cipher-Trainer/static/media/Russo.22432e60.ttf); /* Путь к файлу со шрифтом */
}
@font-face {
    font-family: Computer;
    src: url(/Cipher-Trainer/static/media/computer.f439abec.ttf);
}
.startbutt{
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    margin:0 auto;
    width: 30%;
    background: #5f6b9f;
    color: #dbe0f8;
    display: inline-block;
    padding: .3em 1em;
    border: 1px solid #aaa;
    border-radius: 100px;
    outline: none;
    background-position: calc(100% - 2.7em) 0, calc(100% - 2.7em + 1px) 0, 0 0;
    background-size: 1px 100%, 1px 100%, auto auto;
    overflow: hidden;
}
.startbutt2{
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    margin:0 auto;
    padding: 50px;
    position: center;
    border-radius: 50%;
    background: linear-gradient(green,white) fixed;
    color: black;
}
.divbutt{
   display: flex;
}
.appName
{
    text-align: center;
    alignment: center;
    font-size: 45pt;
    color: #141f50;
    font-family: Russo;
}
.alignmentDiv{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    transform: translate(-50%, -50%);
}
.textstart{
    font-size: 30pt;
    text-align: center;
    color: #000000;
    font-family: Computer;
}
.tipbut{
    margin-left: auto;
    background: blue;
    color: white;
}
.PanelBase .Panel__in{
    background-image: url(/Cipher-Trainer/static/media/fon.a0c5959f.png) !important;
    background-position: center;
}
.choice_one .Panel__in{
    background-image: url(/Cipher-Trainer/static/media/choice_fon.a615986c.png) !important;
    background-position: center;
}
